<template>
  <div class="login">
    <alert
      class="alert"
      v-show="isshow"
      :message="message"
      @close2="closes"
    ></alert>
    <h3>剧本杀后台管理系统</h3>
    <div class="user">
      <p>账号: <input type="text" v-model="username" /></p>
    </div>
    <div class="passward">
      <p>密码: <input type="password" v-model="password" /></p>
    </div>
    <el-button class="logins" @click="login">登陆</el-button>
  </div>
</template>
<script>
import axios from "axios";
import alert from "@/components/alert.vue";
import { logins } from "@/api/login";
export default {
  components: {
    alert,
  },
  data() {
    return {
      username: "",
      password: "",
      isshow: false,
      message: "",
      timer: null,
    };
  },
  methods: {
    closes(e) {
      this.isshow = e;
    },
    login() {
      logins({ username: this.username, password: this.password }).then(
        (res) => {
          console.log(res);
          if (res.code == 200) {
            localStorage.setItem("token", res.token);
            this.$message.success('登陆成功')
            this.$router.push({path:"/home",query:{id:'1'}});
          } else {
            this.timer = setInterval(() => {
              this.isshow = false;
            }, 6000);
            if (this.username == "" || this.password == "") {
              this.isshow = true;
              this.message = "请输入账号密码";
              this.timer;
              clearInterval(this.timer)
            } else {
              this.isshow = true;
              this.message = "账号密码不正确";
              this.timer;
              clearInterval(this.timer)
            }
            clearInterval(this.timer)
          }
        }
      );
    },
    // login(){
    //   this.$router.push('/home')
    // }
  },
  created() {},
  mounted() {
    console.log(localStorage.getItem("token"));
  },
};
</script>
